@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@400;500;700&display=swap');
.App{
    background: url("./main.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    font-family: 'Quicksand', sans-serif !important;
    height: 100vh;height: calc(var(--vh, 1vh) * 100);;
    overflow: hidden;
}
.Post{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;height: calc(var(--vh, 1vh) * 100);
    margin-top: -8%;
}
.Post-container{
    display: flex;
    justify-content: center;
    align-items: center; 
    overflow: hidden;
}
.Post-form-paper{
    padding: 2%;
    height: 60%;
    width: 40%;
}
.grid-image>img{
    height: 100%;
    width: 100%;
}
.post-grid{
    overflow: auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    min-height: 100vh;height: calc(var(--vh, 1vh) * 100);;
}
h1{
    text-align: center;
}
.Auth{
    height: 100vh;height: calc(var(--vh, 1vh) * 100);;
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
}
.Auth>h1{
    font-size: xx-large !important;
}
.Paper{
    padding:4% 2%;
    align-self: center;
    min-width: 60vw;
    margin: 2%;
}
.container>.right{
    display: flex;
    justify-content: center;
    align-items: center;flex-direction: column;
    border-top: 1px solid darkgray ;
}

.google-btn {
    width: 184px;
    height: 42px;
    background-color: #4285f4;
    border-radius: 2px;
    box-shadow: 0 3px 4px 0 rgba(0,0,0,.25);
    position: relative;
  }
  
  .google-icon-wrapper {
    position: absolute;
    margin-top: 1px;
    margin-left: 1px;
    width: 40px;
    height: 40px;
    border-radius: 2px;
    background-color: white;
  }
  .google-icon-wrapper>{
      display: flex;
      justify-content: center;
      align-items: center;
  }
  .google-icon {
    margin-top: 11px;
    width: 18px;
    height: 18px;
  }
  .btn-text {
    float: right;
    margin: 11px 11px 0 0;
    color: white;
    font-size: 14px;
    letter-spacing: 0.2px;
    font-family: "Roboto";
  }

.MuiGrid-item{
    padding: 2%;
}
input,textarea{
    margin: 3%  0 !important;
    border: 1px solid grey !important;
    padding: 10px 10px !important;
    border-radius: 5px !important;
    font-family: 'Quicksand',sans-serif !important;
}
input:focus,textarea:focus{
    /* border: blueviolet !important; */
    outline: blueviolet !important;
}
.custombutton{
    width: 100%;
    justify-self: center;
    background-color: blueviolet;
    color: white;
    /* border: 2px solid lightgray !important;  */
    padding: 10px 5px;
    font-weight: 700;
    border-radius: 5px !important;

}
.custombutton-red{
    width: 100%;
    justify-self: center;
    background-color: transparent;
    color: white;
    border: 2px solid #FE3E57 !important; 
    padding: 10px 5px;
    font-weight: 700;
    border-radius: 5px !important;
    color: #FE3E57;
}
.loader{
    position: absolute;
    top:50%;
    left:50%
}
.MuiCircularProgress-colorPrimary{
    color: white !important;
}
input[type="file" i]{
    background-color: white;
}
.google-button{
    background-color: #4285F4 !important;
    color: white !important;
    font-family: 'Quicksand',sans-serif !important;
    width: 80% !important;
}

.form{
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin: 2% 10%;
}
.form>div{
    margin: 1% 0;
}
button:focus{
    outline: none;
    cursor: pointer;
}
a{
    color: blueviolet !important;
    cursor: pointer;
}
.Navbar-wrapper{
    display: none
}
.Navbar{
    position: sticky;
    top: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 1% 5%;
    background-color: #011F75;
    z-index: 999;
    margin-bottom: 2vh;
    
    /* height: calc(var(--vh, 1vh) * 100);; */

}
.Navbar-left{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex: 0.1
}
h2>a{
    color: white !important;
    text-decoration: none;
    cursor: pointer;
    font-size: xx-large;
}
h2>a:hover{
    color: #7DFFFC !important;
}
.Navbar-center{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    flex: 0.5;
}
.Navbar-center>a{
    color: white !important;
    text-decoration: none;
    cursor: pointer;
    font-weight: 700;
    display: flex;
    justify-content: center;
    align-items: center;
}
.Navbar-center>a:hover{
    color: cyan !important;
}
.Navbar-right{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex: 0.2
}
.logout{
    background-color: transparent;
    border: 2px solid white;
    color: white;
    font-family: 'Quicksand' ,sans-serif;
    border-radius: 5px;
    padding: 5px 7px;
    margin: 10px 10px;
}
.Navbar-res{
    display: flex;
    padding: 2% 3%;
    flex-direction: row;
    justify-content: space-between;
    background-color: #011F75;
    /* display: none */
}
.Navbar-res-left{
    display: flex;
    flex-direction: row;
}
.Navbar-res-right{
    display: flex;
    align-items: center;
}
.Navbar-res-right>svg{
    color:white;
}
.collapse-nav{
    background-color: #011F75;
}
hr{
    background-color: white !important;
}
.MuiListItem-root,.MuiListItem-root>a{
    color: white !important;
    text-decoration: none;
    cursor: pointer;
    font-weight: 700;
    display: flex;
    flex-direction: row;
    align-items: center;
}
.MuiListItem-root:hover,.MuiListItem-root>a:hover{
    color:#7DFFFC !important;
    
}
.Username{
    display: flex;flex-direction: row;align-items: center;
}
svg{
    margin: 3px !important;
}
h2{
    margin: 0 !important;
    color: white !important;
    display: flex;
    align-items: center !important;

}

.Liked>span>svg{
    color: #FE3E57 !important;
}

.Bookmarked>span>svg{
    color: #212121 !important;
}
.err-box{
    display: flex;
    margin: auto 30%;
}
.vl-outer{
    position: relative;
}
.vl {
    position: absolute;
    left: 60% ;
    transform: translate(-50%);
    border: 2px solid #ddd;
    height: 100%;
    display: none;
  }
  
  /* text inside the vertical line */
  .vl-innertext {
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: #f1f1f1;
    border: 1px solid #ccc;
    border-radius: 50%;
    padding: 8px 10px;
  }
@media only screen and (min-width: 768px) {
    .right{
        border-top: none !important;
    }
   
    .loader{
        left: 50%
    }
    .vl{
        display: block;
    }
    .Navbar-res,.collapse-nav{
        display: none;
    }
    .Navbar-wrapper{
        display: block;
    }
}
